<template lang="pug">
  .card(v-touch:swipe="swipeHandler")
    .card-body(v-if="game.specialCard")
      div {{ game.currentSpecialCard.name }}
      div {{ game.currentSpecialCard.disc }}    
    .card-body(v-else)
      div.card-category(v-if="currentCard.category" :class="['color'+this.currentCard.catIndex]") {{ currentCard.category }}
      div.content
        div {{ currentCard.name }}
        div {{ currentCard.disc }}
        div(v-if="currentCard.cost > 0") Price: {{ currentCard.cost }}
        div(v-if="currentCard.rent[currentCard.houses]>0") Rent: {{ currentCard.rent[currentCard.houses] }}
        div(v-if="currentCard.index===10")
          | Узники АЗКАБАНА:
          ul
            li(v-for="ppl in game.jail" :key="ppl.id") {{ ppl.name }}
      
      div.owner(v-if="getOwner" :class="['owner-color' + getOwner.index]") Владелец: {{ getOwner.name }}
        
</template>

<script>
import { mapGetters, mapState, mapActions } from 'vuex'
export default {
  name: 'MainCard',
  computed: {
    ...mapState(['game']),
    ...mapGetters(['currentCard', 'getOwner']),    
  },
  methods: {
    ...mapActions(['startTurn', 'doAction', 'endTurn']),
    swipeHandler(e){
      if(e==='left'||e==='right'){
        if(this.game._action){
          this.doAction();
        } else if(!this.game._endOfTurn) {
          this.endTurn();
        } else {
          this.startTurn();
        }        
      } 
    },
  }
}
</script>

<style lang="scss" scoped>
  .card{
    height: 250px;
    width: 170px;
    margin: 0 auto;
    border: solid 2px #300300;    
    .card-category{
      flex: 0 1 auto;
      margin: 0;
      line-height: 40px;
      border-bottom: 2px solid #300300;
    }
    .content{
      flex: 1 1 auto;
    }
    .owner{
      flex: 0 1 auto;
      height: 20px;
      font-size: 10px;
      border-top: 2px solid #300;
    }
    .card-body{
      display: flex;
      flex-flow: column;      
      height: 100%;
      p {
        padding: 0 5px;
        margin: 0;
      }
    }
  }

  $colors: (
    1: #8819c9,
    2: #10a2b6,
    3: #ce1e67,
    4: #c76413,
    5: #dd0000,
    6: #ffee00,
    7: #1eb400,
    8: #2f0ee9,
    100: #723939,
    200: #4d5f88,
    1000: #467050
  );

  @each $number, $color in (0, #ff484846), (1, #3939ff31), (2, #92dd9252), (3, #ffa60054){
      .owner-color#{$number}{
        background-color: $color;
      }
  }

  @each $n, $color in $colors {
    .color#{$n}{
      @if $n==6 {color: #300300;}
      @if $n!=6 {color: white;}
      background: $color;
      cursor: pointer;     
    }
  }
</style>