<template lang="pug">
  div.player-cards Ваши карты:
    .cards-board(v-if="playerCards.length<=16")
      div(v-for="card in playerCards" 
        :key="card.id" 
        :class="['color'+card.catIndex]"
        @click="selectCard(card)"
        ) {{ (card.fullRent ? '* ' : '') + card.name.slice(0,10) + '...' }}
    .cards-board(v-else)
      div(v-for="card in playerCards" :key="card.id" :class="['color'+card.catIndex]" @click="selectCard(card)") {{ (card.fullRent ? '* ' : '') + card.name.slice(0,5) + '...' }}
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'PlayerCards',
  computed: {
    ...mapGetters(['playerCards']),
  },
  methods: {
    selectCard(card){
      console.log(card);
    }
  }
}
</script>

<style lang="scss" scoped>
  ul{
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  .player-cards{
    margin-top: 10px;
  }
  .cards-board{   
    margin: 0 auto;
    width: 300px;
    height: 80px;
    font-size: 12px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: left;
    div{
      box-sizing: border-box; 
      border: 1px solid rgb(105, 16, 16);
      height: 20px;
    }  
  }

  $colors: (
    1: #8819c9,
    2: #10a2b6,
    3: #ce1e67,
    4: #c76413,
    5: #dd0000,
    6: #ffee00,
    7: #1eb400,
    8: #2f0ee9,
    100: #723939,
    200: #4d5f88,
    1000: #467050
  );

  @each $n, $color in $colors {
    .color#{$n}{
      @if $n==6 {color: #300300;}
      @if $n!=6 {color: white;}
      background: $color;
      cursor: pointer;     
    }
  }

</style>