<template lang="pug">
  section.start-screen(v-if="!gameStarted")
    p Начните игру
    p Новая игра
    div(@click='startGame(2)') (2 игрока)
    div(@click='startGame(3)') (3 игрока)
    div(@click='startGame(4)') (4 игрока)

  section.game-board(v-else)
    board
    main-card
    div(v-if="game.auction")
      div АУКЦИОН
      div Цена лота: {{ game.auction.bet }}
      div Делает ставку: {{game.auction.current.name}}
      input(ref="bet")
      button(@click="accept") V
      button(@click="cancelAuc") X
    div(v-else)      
      .actions        
          button(@click="startTurn" :disabled="game._action || !game._endOfTurn") Ход   
          button(@click="doAction" :disabled="!game._action") Действие       
          button(@click="endTurn" :disabled="game._action || game._endOfTurn") Закончить
          button(:disabled="game._endOfTurn") Обменятся
          button(@click="dontBuy" v-show="game._action && currentCard.canBuy") Не покупать        
          button(@click="payFine" v-show="checkJail") Выйти из тюрьмы за 50М          
      div {{ game.cubes }}
      player-info      
    player-cards
</template>

<script>

import { mapState, mapMutations, mapGetters, mapActions } from 'vuex'
import Board from './components/Board';
import PlayerInfo from './components/PlayerInfo';
import PlayerCards from './components/PlayerCards';
import MainCardVue from './components/MainCard.vue';
import draggable from 'vuedraggable';

export default {
  components:{
    board: Board,
    "player-info": PlayerInfo,
    "player-cards": PlayerCards,
    "main-card": MainCardVue,
    draggable,
  },  
  computed: {
    ...mapState(['gameStarted', 'game']),
    ...mapGetters(['currentCard', 'getOwner', 'playerCards', 'checkJail'])
  },
  methods: {
    ...mapActions(['startTurn', 'doAction', 'endTurn', 'dontBuy', 'acceptAuc', 'cancelAuc', 'payFine']),
    ...mapMutations(['startGame']),
    accept(){
      this.acceptAuc(this.$refs.bet.value);
    },   
  },
}
</script>

